import React, { useEffect } from "react";
import s from './dialogs.module.css'
import Name from './Names/Name';
import Messeges from './Messeges/Messeges';
import DialogsForm from './DialogsForm/DialogsForm';
import { reduxForm } from "redux-form";

let Dialogs = React.memo((props) => {

    // componentDidMount() {
    //     props.stopLoader()
    // }

    useEffect(() => {
        props.stopLoader()
    },[props.stopLoader])


    let NameEndData = props.state.NameData.map(item => <Name name={item.name} id={item.id} />)

    let MessEndData = props.state.MessData.map(item => <Messeges mymes={item.mymes} youmes={item.youmes} />)

    let onSubmit = (data) => {
        props.newMessage(data.messege)
    }

    return (
        <div className={s.dialogs}>
            <div className={s.dialogs__names}>
                <h1 className={s['dialogs__names-title']}>Dialogs</h1>
                {NameEndData}
            </div>
            <div className={s.messege__wrapper}>
                {MessEndData}
                <ReduxFormDialog onSubmit={onSubmit} />
            </div>
        </div>
    )

})

let ReduxFormDialog = reduxForm({
    form: 'dialogs'
})(DialogsForm)

export default Dialogs
