import React from "react";
import { newMessegeActionCreator} from '../../redux/dialogs-reducer';
import Dialogs from './Dialogs';
import { connect } from "react-redux";
import RedirectHoc from './../../hocs/RedirectHoc';
import { compose } from "redux";
import { stopLoader } from './../../redux/finding-reducer';

let f1 = (state) => {
    return {
        state: state.dialogsData,
        statusLogin: state.authData.status
    }
}

let f2 = (dispatch) => {
    return {
        newMessage: (text) => {
            dispatch(newMessegeActionCreator(text))
        },
        stopLoader: () => {
            dispatch(stopLoader())
        }
    }
}

// connect(f1,f2)(RedirectHoc(Dialogs))

const DialogsContainer = compose(
    connect(f1,f2),
    RedirectHoc 
)(Dialogs)

export default DialogsContainer