import React from "react";
import s from './messeges.module.css'
import MyMessege from './MyMessege/MyMessege';
import YouMessege from './YouMessege/YouMessege';

const Messeges = (props) => {
    if (props.mymes != ""){
        return(
            <div className={s.dialogs__messages}>
                <MyMessege mymes={props.mymes}/>
            </div>
        )  
    } else if (props.youmes != "") {
        return(
            <div className={s.dialogs__messages}>
                <YouMessege youmes={props.youmes}/>
            </div>
        )
    }
}

export default Messeges

