
import s from '../dialogs.module.css'
import { MessegeTextarea } from './../../../comon/textarea/MessegeTextarea';
import { maxLength, required } from '../../../comon/validator/validator';
import { FieldCreation } from './../../../comon/Field/Field';

const DialogsForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <div className={s['messege__textarea-box']}>
                {FieldCreation(s.messege__textarea,'','messege',[required,maxLength(500)],MessegeTextarea)}
                <button className={s.messege__btn}></button>
            </div>
        </form>
    )
}

export default DialogsForm