import s from './MessegeTextarea.module.css'

export const MessegeTextarea = (props) => {
    return (
        <div  className={s.block}>
            <textarea {...props.input} className={
                `${s.textarea} ${props.meta.error && props.meta.touched && s.textarea__error}`
            }></textarea>
            <span className={props.meta.error && props.meta.touched && s.error__text}>{props.meta.touched && props.meta.error}</span>
        </div>
    )
}

